.home {
    background: #FFF;
    height: 100%;
}
.home .header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 0px 10px;
}
.home .header .logo{
    width: 140px;
    height: 56px;
}
.home .header .right {
    width: 100px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-line-pack: center;
        align-content: center;
}
.home .search {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    background: #FFF;
    height: 40px;
    border: 1px solid rgba(0, 0, 0, 0.55);
    border-radius: 5px;
    margin: 0px 10px;
}
.home .search object{
    width: 20px;
    height: 20px;
    color: rgba(0, 0, 0, 0.8);
}
.home .search input {
    width: 70%;
    border: 0px;
    margin-left: 5px;
}
.home h3 {
    text-align: center;
    margin-top: 10px;
}
.home .bookList {
    position: absolute;
    top:140px;
    left: 10px;
    right: 10px;
    bottom: 5px;
    border-bottom: 1px solid #f5f5f5
}



