.nav {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    background: #ffffff;
    height: 100%;
    margin-top: 5px;
}
.nav .navContent {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    height: 90px;
    padding-bottom: 5px;
}
.nav .navContent i {
    display: block;
    border-bottom: 5px solid transparent;
    width: 20px;
    border-radius: 3px;
}
.nav .selected span {
    color: #51C0F6;
}
.nav .selected i {
    border-bottom: 5px solid #51C0F6;
}


