.searchTab .searchBox {
    background: #fff;
    padding: 10px 0;
    margin-top: 5px;
}
.searchTab .searchBox .searchBar {
    width: 340px;
    height: 40px;
    border: 1px solid rgba(0, 0, 0, 0.3);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    border-radius: 5px;
    margin: auto;
    background: #fff;
    overflow: hidden;
}
.searchTab .searchBox .searchBar input {
    width: 270px;
    height: 100%;
    border: 0px;
}
.searchTab .searchBox .searchBar .searchIcon {
    width: 50px;
    height: 100%;
    background: #f5f5f5;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
}
.searchTab .searchBox .tagBox {
    width: 100%;
    padding: 10px 0 0 10px;
    overflow: hidden;
}
.searchTab .searchBox .tagBox p{
    display: inline-block;
    float: left;
}
.searchTab .searchBox .tagBox .tag1 {
    font-size: 12px;
    color: rgba(0, 0, 0, 0.3);
}
.searchTab .searchBox .tagBox .tag2 {
    font-size: 25px;
    color: #FED466;
}
.searchTab .searchBox .tagBox .tag3 {
    font-size: 17px;
    color: #CCCCCC;
}
.searchTab .searchBox .changeBox {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}
.searchTab .searchBox .changeBox hr {
    -ms-flex:1 1;
        flex:1 1;
    opacity: 0.3;
}
.searchTab .searchBox .changeBox .word {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    margin: 0 5px;
    width:80px;
    font-size: 16px;
}
.searchTab .bookList {
    background: #fff;
    padding: 0 10px;
}


