.advContainer{
    width: 750px;
    height: 252px;
    position: relative;
}
.imglist {
    width: 100%;
    height: 100%;
    overflow: hidden;
}
@keyframes fade-in {  
    0% {opacity: 0.3;}/*初始状态 透明度为0*/  
    40% {opacity: 0.8;}/*过渡状态 透明度为0*/  
    100% {opacity: 1;}/*结束状态 透明度为1*/  
}  
@-webkit-keyframes fade-in {/*针对webkit内核*/  
    0% {opacity: 0.3;}  
    40% {opacity: 0.8;}  
    100% {opacity: 1;}  
}  
.imglist ul {
    position: relative;
    padding: 0px;
    margin: 0px;
}
.imglist ul li {
    list-style-type: none;
    width: 750px;
    height: 100%;
    display: none;
    top: 0px;
    /* transition: left 1s; */
    -webkit-animation: fade-in;
            animation: fade-in;/*动画名称*/  
    -webkit-animation-duration: 1s;  
            animation-duration: 1s;/*动画持续时间*/  
    -webkit-animation:fade-in 1s;/*针对webkit内核*/ 
}
.imglist ul .active {
    display: inline-block;
}
.imglist ul li img {
    width: 100%;
    height: 100%;
}
.dot {
    width: 100%;
    height: 40px;
    position: relative;
    top: -40px;
}
.dot ul {
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    padding: 0px;
    margin: 0px;
}
.dot ul li {
    height: 100%;
    list-style-type: none;
    text-align: center;
    display: inline-block;
    border-left: 1px solid white;
    width: 150px;
}
.dot ul li:hover {
    background: red;
}
.dot ul li a {
    color: white;
    margin-top: 10px;
    font-size: 14px;
    display: inline-block;
}


