.gonggao {
    padding-left: 20px;
    width: 270px;
}
.gonggao .title {
    padding: 10px 0;
    font-size: 20px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.8);
}
.gonggao ul {
    padding:0px;
    margin: 0px;
}
.gonggao ul li {
    list-style-type: none;
    padding:5px 0;
    font-size: 15px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}


