.novebox {
    width: 247px;
    /* height: 148px; */
    overflow: hidden;
    display: inline-block;
}
.novebox .boximg {
    width: 74px;
    height: 96px;
    float: left;
    border-radius: 2px;
    -webkit-box-shadow: 0 1px 6px rgba(0,0,0,.3), 0 0 5px #f9f2e9 inset;
            box-shadow: 0 1px 6px rgba(0,0,0,.3), 0 0 5px #f9f2e9 inset;
}
.novebox .boximg img {
    width: 100%;
    height: 100%;
}
.novebox .box {
    width: 150px;
    height: 96px;
    float: left;
    margin-left: 20px;
    overflow: hidden;
}
.novebox .box .title {
    font-size: 16px;
    color: rgba(0, 0, 0);
}
.novebox .box .desc {
    margin-top: 10px;
    font-size: 12px;
    color: rgba(0, 0, 0, 0.7);
}
.novebox .box .desc .footer {
    width: 150px;
    height: 35px;
    overflow: hidden;
    font-size: 10px;
    color: rgba(0, 0, 0, 0.5);
}
.novebox .box .desc .footer .tag {
    float: left;
}
.novebox .box .desc .footer .catalog {
    float: right;
}



