.book {
    display: -ms-flexbox;
    display: flex;
    border-top: 1px solid #f5f5f5;
    padding: 10px 0px;
}
.noTopBorder {
    border-top: 0px;
}
.book .bookinfo {
    margin-left: 10px;
}
.book .bookimg img {
    width: 77px;
    height: 110px;
}
.book .title {
    text-align: left;
    padding: 0px;
    margin: 0px;
}
.book .author {
    color: rgba(0, 0, 0, 0.6);
    font-size: 13px;
    margin-top: 5px;
}
.book .desc {
    color: rgba(0, 0, 0, 0.6);
    font-size: 15px;
    margin-top: 20px;
}


